.tile{
    transition:.5s;
    display: flex;
    position: relative;
    width: fit-content;
    margin: 0px;
    float:left;
    flex-wrap: nowrap;
}
.tile:hover{ 
transform:scale(1.05);
opacity: 80%;
}

.tile-layout{
    width: 380px;
    height: 234px;
    display: flex;
    border-radius: 15px;
    border-style: hidden;
    border-color: blueviolet;
    background-color: blueviolet;
    white-space: nowrap;
    flex-direction: column;
    margin: 15px;
    position: relative;
    
}

.tile-layout :hover{
    opacity: 80%;
}

.tile-image-outer{
    border-radius: inherit;
}

.tile-image{
    height: 200px;
    width: 100%;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    margin: 0;
    padding: 0;
    opacity: 100%;
}
/* .tile-image:hover{
    background-color: #F9BE32;
} */


.tile-info{
    display: flex;
    justify-content: center;
}

.tile-name{
    display: flex;
    justify-content: center;
    color: white;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 23px;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.tile-edit-button{
    display: none;
}

.tile-edit-button-active{
    position: absolute;
    right: 10px;
    margin-top: 5px;
    font-size: 20px;
    color:#F9BE32;
    z-index: 3;
}

.tournament-overlay{
    display: none;
}

.tournament-overlay-active{
    position: fixed;
    display: block;
    z-index: 15;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}

.tournament-overlay-screen{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0,0,0,0.5);
    cursor: pointer;
}

.tournament-edit-outer{
    z-index: 17;
    position: fixed;
    /* left: 40%; */
    top: calc(100vh/2 - 250px);
    left: calc(100vw/2 - 150px);
}

.tournament-view-outer{
    z-index: 17;
    position: fixed;
    width: 85vw;
    top: 50%;
    left: 50%;
    transform: translate(-52%, -50%);
}